/* body */
*,
*::after,
*::before {
  box-sizing: border-box;
}

::selection {
  background: $color-primary;
  color: white;
}

html {
  // 62,5% of 16 px = 10px
  font-size: 62.5%;
}

body {
  font-family: Inter, Arial, Helvetica, sans-serif;
  color: $color-body;
  font-size: 2.4rem;
  line-height: 1.5;
}

h1,
h2,
h3 {
  color: $color-headings;
  margin-bottom: 1rem;
}

h1 {
  font-size: 7rem;
}
h2 {
  font-size: 4rem;
}
h3 {
  font-size: 3rem;
  font-weight: 500;
}

p {
  margin-top: 0;
}

@media screen and (min-width: 1024px) {
  body {
    font-size: 1.8rem;
  }
  h1 {
    font-size: 8rem;
  }
  h2 {
    font-size: 4rem;
  }
  h3 {
    font-size: 2.5rem;
  }
}
